.Script {
    width : 100vw;
    min-height: 100vh;
    padding : 0;
    margin : 0;
    overflow-x: hidden;
}

.sContainer {
    position: relative;
    top : 100px;
    left : 0;
    width : 100%;
    min-height : 100%;
    padding : 0;
    margin : 0;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}

