.menubar {
    position: fixed;
    width : 100%;
    min-height : 10vh;
    padding : 0;
    margin : 0;
    overflow: hidden;
    display :flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    background-color: transparent;
    transition: background-color 0.5s ease;
}

.menubar.active {
    background-color: rgb(227, 227, 227);
}

.header {
    width : 90%;
    height : 100%;
    margin : 0;
    margin-top: 10px;
    display : flex;
    justify-content: space-between;
    align-items: center;
}

.menulist1 {
    display : flex;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    padding : 0;
    margin : 0;
}

.menulist1 li {
    margin-left: 30px;
}

.menulist1 li:hover {
    cursor: pointer;
}

.menulist1 li p {
    margin : 0;
    padding : 0;
    padding-bottom: 10px;
    font-size: 20px;
    color : black;
}

.menulist1 li p:hover {
    color: #00D8FF;
}

.menulist2 li p:hover {
    color: #00D8FF;
}

.menulist1 img {
    width : 150px;
    height : 70px;
}

.menulist2 {
    display : flex;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    padding : 0;
    margin-right: 3%;
}

.menulist2 li {
    display : flex;
    justify-content: center;
    align-items: center;
    margin-right: 30px;
}

.menulist2 li:hover {
    cursor: pointer;
}

.menulist2 li:nth-child(1) img {
    width : 50px;
    height : 50px;
}

.dropdown {
    position: absolute;
    width : 200px;
    height : 40px;
    top: 30px;
    right: 16%;
    background-color: transparent;
    z-index: 999;
    padding: 8px 0;
    display : flex;
    justify-content: center;
}

.dropdown li {
    display : flex;
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0 0 0 30px;
}

.dropdown li a p {
    text-align: center;
}

.toMenu {
    text-decoration: none;
}

.toLogin {
    text-decoration: none;
}

.toLogin p {
    color : black;
    font-size: 20px;
}