.MissionForm {
    width : 100vw;
    min-height : 100vh;
    padding : 0;
    margin : 0;
    left : 0;
    top : 0;
    display : flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}

.mfContainer {
    width : 85%;
    min-height : 80vh;
    margin : 30px 0 0 0;
    padding : 0;
    display : flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    animation: fadeUp 0.5s ease-out forwards;
}

.miLeft {
    position : sticky;
    width : 30%;
    height : 80vh;
    margin : 0;
    padding : 0;
    display : flex;
    flex-direction: column;
}

.miTitle {
    width : 100%;
    height : 30%;
    margin : 0;
    padding : 0;
}

.miBtn {
    width : 100%;
    height : 70%;
    margin : 0;
    padding : 0;
    display : flex;
    justify-content: space-between;
    align-items: center;
}

.miTitle h1 {
    font-size: 2.7rem;
    font-family: 'Poppins';
}

.miTitle p {
    font-family: 'Poppins';
    font-size : 16px;
    color : #666;
}

.miBtn {
    margin-bottom: 20%;
}

.miBtn button {
    width : 140px;
    height : 60px;
    background-color: #13C1F9;
    border-radius: 20px;
    border : 1px solid #13C1F9;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
    color: #fff;
    font-size: 20px;
    margin-left: 5%;
}

.miBtn button:hover {
    background-color: #11a7d9;
    cursor : pointer;
}
.miRight {
    width : 70%;
    min-height : 80vh;
    margin : 0;
    margin-top: 7%;
    padding : 0;
}

.image-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4개의 열로 나눔 */
    gap: 10px; /* 이미지 사이의 간격 설정 */
}

.image-item {
    position: relative;
    overflow: hidden;
}

.image-item img {
    width: 100%; 
    height: 100%;
    object-fit: cover;
    transition: opacity 0.3s ease-in-out;
}

.image-item img:hover {
    opacity: 0.8;
    cursor: pointer;
  }