.ScriptUpForm {
    width : 100vw;
    min-height : 150vh;
    padding : 0;
    margin : 0;
    left : 0;
    top : 0;
    display : flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}

.sufContainer {
    width : 80%;
    height : 80%;
    margin : 30px 0 0 0;
    padding : 0;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.09);
    border-radius: 20px;
    animation: fadeUp 0.5s ease-out forwards;
}

.sufBox {
    width : 100%;
    height : 70%;
    margin : 0;
    padding : 0;
}

.sufTitle {
    width : 100%;
    height : 30%;
    margin : 0;
    padding : 0;
    display : flex;
    align-items: center;
}

.sufTitle button {
    width : 50px;
    height : 50px;
    margin : 0;
    padding : 0;
    background-color: transparent;
    border: none;
}

.sufTitle h1 {
    color: #13C1F9;
    margin-left: 35%;
}

.sufTitle button {
    margin-left: 3%;
}

.sufTitle button img {
    width : 100%;
    object-fit: cover;
}

.back:hover {
    cursor: pointer;
}

.sufContents {
    width : 90%;
    height : 70%;
    margin : 0;
    padding : 0;
    display : flex;
    justify-content: center;
    align-items: center;
}

.inImage {
    width : 30%;
    height : 80%;
    padding : 0;
    margin : 0;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #13C1F9;
    border-radius: 20px;
}

.Image {
    width : 100%;
    height : 100%;
    margin : 0;
    padding : 0;
    display : flex;
    justify-content: center;
    align-items: center;
}

.Image img {
    width : 50%;
    height : 50%;
    object-fit: contain;
}

.UploadPrompt {
    width : 100%;
    height : 50%;
    margin : 0;
    padding : 0;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.UploadPrompt label {
    position : relative;
    left: 40px;
}

label.custom-file-upload input[type="file"] {
    display: none;
}

label.custom-file-upload {
    display: inline-block;
    width : 90px;
    height : 25px;
    margin-right: 21%;
    padding : 10px 0 10px 20px;
    cursor: pointer;
    color: #fff;
    font-weight: bold;
    font-size: 16px;
    white-space: nowrap;
    vertical-align: middle;
    background-color: #13C1F9;
    border-color: #13C1F9;
    border-radius: 10px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
}

label.custom-file-upload:hover {
    background-color: #108fba;
}

.inText {
    width : 60%;
    height : 80%;
    margin : 0;
    padding : 0;
    display : flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.sun-editor {
    overflow-y: hidden;
    margin : 10% 0 0 30%;
}

.check2 {
    margin-bottom: 20px;
}

.sufInput {
    width : 100%;
    height : 30%;
    margin : 50px 0 0 0;
    padding : 0;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ctBox2 {
    width : 100%;
    margin : 0 0 15px 0;
    padding : 0;
    display : flex;
    flex-direction: column;
    padding-left: 50%;
}

.ctBox2 p {
    font-size: 20px;
    font-family: 'Poppins';
    font-weight: 600;
    margin : 0;
    padding : 0;
    color : #555;
}

.ctBox2 input {
    width : 50%;
    height : 50px;
    border-radius: 10px;
    padding-left: 10px;
    border : 1px solid #ccc;
}

.ctBox2 input:active {
    border: 1px solid #13C1F9;
}