@import url('https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap');

.ScriptMenu {
    width : 100vw;
    height : 10vh;
    margin : 0;
    padding : 0;
    display: flex;
    justify-content: center;
}

.smContainer {
    width : 84%;
    height : 100%;
    margin : 0;
    padding : 0;
    display : flex;
    justify-content: space-between;
    align-items: center;
}

.leftBox {
    width : 50%;
    height : 100%;
    margin : 0;
    padding : 0;
    display : flex;
    align-items: center;
}

.leftBox h1 {
    color : black;
    font-family: 'Poppins';
    font-size: 2rem;
    font-weight: 600;
}

.sfCategory {
    display : flex;
    list-style-type: none;
}

.sfCategory li {
    margin-right: 10px;
}

.sfCategory li p {
    padding-right : 10px;
    color : #cbcbcb;
    font-weight: 600;
}

.sfCategory li:nth-child(1) p {
    border-right: 2px solid #acacac;
    color : black;
} 

.sfCategory li:nth-child(2) p {
    border-right: 2px solid #acacac;
}

.sfCategory li p:hover {
    color : black;
    cursor: pointer;
}

.toUpload {
    width : 40px;
    height : 40px;
    font-size: 36px;
    border: 1px solid #13C1F9;
    background-color: #fff;
    color: #13C1F9;
}

.toUpload:hover {
    cursor: pointer;
    background-color: #13C1F9;
    color: #fff;
    border: 1px solid #fff;
    transition: 0.5s;
}

