.hbContainer {
    width : 100vw;
    height: 100vh;
    margin : 0;
    padding : 0;
    top : 0;
    left : 0;
    z-index: 1;
}

.banner {
    width : 100%;
    height : 100%;
    padding : 0;
    margin : 0;
    top : 0;
    left : 0;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.banner img {
    top : 0;
    left : 0;
    width : 100%;
    height : 100%;
    object-fit: auto;
    overflow-y: hidden;
}

.bannerTxt1 {
    position: absolute;
    top : 16%;
    left : 8%;
    color: #10A6D6;
    font-size: 3.5rem;
    font-weight: bolder;
}

.bannerTxt2 {
    position: absolute;
    top : 16%;
    right: 7%;
    color: #10A6D6;
    font-size: 3.5rem;
    font-weight: bolder;
}