@import url('https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap');

.ScriptForm{
    width : 100vw;
    min-height : 90vh;
    margin : 0;
    padding : 0;
    display : flex;
    justify-content: center;
    overflow-x: hidden;
}

.sfContainer {
    width : 85%;
    min-height : 100vh;
    margin : 0;
    padding : 0;
    justify-content: center;
}

.sfBox {
    width : 100%;
    height : 25vh;
    margin : 0;
    padding : 0;
    display : flex;
    background-color: #fff;
    margin-top: 2%;
    transition: box-shadow 0.3s ease;
    border-radius: 20px;
}

.sfBox:hover {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);;
    cursor: pointer;
}

.sfLeft {
    width : 20%;
    height : 100%;
    margin : 0;
    padding : 0;
    display : flex;
    justify-content: center;
    align-items: center;
}

.sfLeft img {
    width : 100%;
    height : 100%;
    object-fit: cover;
    border-radius: 20px 0px 0px 20px;
}

.sfRight {
    width : 80%;
    height : 100%;
    margin : 0;
    padding : 0;
}






